body {
  margin: 0 auto;
  padding: 0;
  font-family: sans-serif;
  background: rgb(201, 55, 55);
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(201, 55, 55, 1)),
    color-stop(0%, rgba(105, 212, 221, 1)),
    to(rgba(0, 0, 0, 1))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(201, 55, 55, 1) 0%,
    rgba(105, 212, 221, 1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: -o-linear-gradient(
    left,
    rgba(201, 55, 55, 1) 0%,
    rgba(105, 212, 221, 1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(201, 55, 55, 1) 0%,
    rgba(105, 212, 221, 1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  height: 100%;
}

.container {
  width: 50%;
  height: 100%;
  margin: 0 auto;
}



